import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import { Toolbar } from '@material-ui/core';
import AlertQbook from '../Components/alert/AlertQbook';
import AlertQbank from '../Components/alert/AlertQbank';
import AlertWeekly from '../Components/alert/AlertWeekly';
import AlertMonthly from '../Components/alert/AlertMonthly';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function Alert() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  useEffect(()=>{
    document.title="Teacher Alert | Qrioctybox"
      },[])

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <div style={{ paddingLeft: 56 }}>
      <Toolbar />
      <div className={classes.root}>
        <AppBar position="static" color="default">
          <Tabs
            value={value}
            onChange={handleChange}
            indicatorColor="primary"
            textColor="primary"
            variant="scrollable"
            scrollButtons="auto"
          >
            <Tab label="Q Book" {...a11yProps(0)} />
            <Tab label="Q Bank" {...a11yProps(1)} />
            <Tab label=" Weekly Test" {...a11yProps(2)} />
            <Tab label=" Monthly Test" {...a11yProps(3)} />
          </Tabs>
        </AppBar>
        <TabPanel value={value} index={0}>
          <AlertQbook name="QBook" />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <AlertQbank name="Qbank" />
        </TabPanel>
        <TabPanel value={value} index={2}>
          <AlertWeekly name="WeeklyTest" />
        </TabPanel>
        <TabPanel value={value} index={3}>
          <AlertMonthly name="MonthlyTest" />
        </TabPanel>
      </div>
    </div>
  );
}
